<template>
	<div class="container">
        <div class="blog-work" v-for='work in works' :key='work.index'>
            <div v-if='work.id === proId'>
                <FormWork :proId='proId' :work='work' />
            </div>
        </div>
        <!-- /.menu-item -->
	</div>
</template>
<script>
import { db } from '@/main';
const FormWork = () => import('./FormWork');
export default {
    name: 'workEdit',
    data() {
        return {
            proId: JSON.parse(localStorage.getItem('workId')) || this.$route.params.id,
            works: [],
        }
    },
    firestore () {
        return {
            works: db.collection('works').orderBy('createdAt')
        }
    },
    components: {
        FormWork
    }
}
</script>
